export default {
	data() {
		return {
			// 输入的昵称
			uname: ''
		}
	},
	methods: {
		// 清除输入框里的内容
		remove() {
			this.uname = ''
		},
		// 返回个人中心页面
		complete() {
			uni.showModal({
				content: '用户名已更新',
				showCancel: false,
				confirmText: '返回',
				confirmColor: '#59b9a6',
				success: (res) => {
					if (res.confirm) {
						uni.switchTab({
							url: '/pages/me/me'
						});
					}
				}
			});
		}
	},
	computed: {
		isInput() {
			if (this.uname == '') {
				return false
			}
			return true
		}
	},
}
